@import url('https://fonts.googleapis.com/css2?family=Changa:wght@200;300;400&family=Coda&family=Rajdhani:wght@300;400&family=Unica+One&display=swap');

.report {
    font-family: 'Coda', cursive;
    /* font-family: 'Changa', sans-serif; */
    /* font-family: 'Rajdhani', sans-serif; */
    /* font-family: 'Unica One', cursive; */
    color: #555;
    margin-top: .1rem;
    display: flex;
    flex-direction: column;
    /* overflow-y: scroll; */
    border-radius: 20px;
    /* margin-bottom: 8rem; */
    /* background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%); */
  /* background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%); */
}

.con-dev{
  /* background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%); */
  /* background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%); */
    padding: 1rem;
    border-radius: 30px;
    margin: 1.4rem 0;
}

.title-wed{
    text-align: center;
    padding: 0rem .8rem;

}

.title-wed > p, h1{
    margin: .6rem;
    font-size: 2rem;
}

.degree{
    margin: .8rem;
}

.dgree-in{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;

}

.dgree-in-con{
    margin: .6rem;
}
    
.dgree-tit {
    text-align: center;
    font-size: 2.4rem;
}


.table-info{
    /* background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%); */
    display: flex;
    flex-direction: row;
    margin: .6rem;
    padding: .4rem;
    box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
    margin-bottom: 2rem;
    border-radius: 30px;

}
.table-info-in{
    flex: .5;
}

.subtitle{
    font-family: 'Changa', sans-serif;
    padding: 2rem;
    text-align: center;
    font-size: 2rem;
}
.developer{
    font-family: 'Changa', sans-serif;
    margin-bottom: 2rem;
    text-align: center;
    font-size: .9rem;
}

@media only screen and (max-width: 600px) {
    .table-info{
        flex-direction: column;
    }

  }